/**
 * @WordPress dependencies
 */
import { RawHTML } from '@wordpress/element';
import { useBlockProps } from '@wordpress/block-editor';

/**
 * @Internal dependencies
 */
import { getCusomPadding } from '@helper';
import { LsIcon } from '@components';
import { migrateIconName } from '@helper/migrate';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * svgタグにクラスを付与
 */
function addClassToSvg(html, addClass) {
	// svgタグにclassを当てる
	const svgMatched = html.match(/<svg.*?>/);
	if (!svgMatched) return html;

	const svgTag = svgMatched[0];

	let newSvgTag = svgTag;

	// すでに追加したいクラス名を持つ場合は何もしない
	const searchClassPattern = new RegExp(`${addClass}`);
	if (-1 !== svgTag.search(searchClassPattern)) {
		return html;
	}

	// クラスを持つかどうか。(クオートコメントはPoeditのエラー回避用)
	if (
		-1 !== svgTag.search(/class=["']/) // '"
	) {
		newSvgTag = svgTag.replace(/class=(["'])/, `class=$1${addClass} `); //'"
	} else {
		newSvgTag = svgTag.replace('<svg', `<svg class="${addClass}"`);
	}

	return html.replace(svgTag, newSvgTag);
}

const IconContentV1 = ({ icon, label, oldIcon, iconHtml, link, ...props }) => {
	// SVG手動入力の場合
	if (iconHtml) {
		// <svg>にclassを当てる
		iconHtml = addClassToSvg(iconHtml, `${blockName}__svg`);

		// リンクがある場合
		if (null !== link) {
			return (
				<a
					{...link}
					{...props}
					data-html='1'
					dangerouslySetInnerHTML={{
						__html: iconHtml,
					}}
				></a>
			);
		}

		return (
			<span
				{...props}
				data-html='1'
				dangerouslySetInnerHTML={{
					__html: iconHtml,
				}}
			/>
		);
	}

	let theIcon = null;
	if (oldIcon) {
		theIcon = <RawHTML>{oldIcon}</RawHTML>;
	} else if (icon) {
		theIcon = <LsIcon icon={icon} label={label || null} className={`${blockName}__svg`} />;
	}

	// リンクがある場合
	if (null !== link) {
		return (
			<a {...link} {...props}>
				{theIcon}
			</a>
		);
	}

	return <span {...props}>{theIcon}</span>;
};

/**
 * 通知ブロック
 */
const blockName = 'ark-block-icon';
export default [
	// 2.20でのアイコンリニューアルより前
	{
		supports: {
			anchor: true,
			className: false,
		},
		attributes: {
			icon: {
				type: 'string',
				default: 'IoSunny',
			},
			label: {
				type: 'string',
				source: 'attribute',
				selector: '.ark-block-icon__svg',
				attribute: 'aria-label',
			},
			iconColor: {
				type: 'string',
			},
			style: {
				type: 'string',
			},
			size: {
				type: 'string',
				default: '5em',
			},
			radius: {
				type: 'string',
			},
			padding: {
				type: 'object',
			},
			position: {
				type: 'string',
			},
			isContentBox: {
				type: 'boolean',
				default: false,
			},
			iconHtml: {
				type: 'string',
				source: 'html',
				selector: '[data-html="1"]',
			},
			href: {
				type: 'string',
				source: 'attribute',
				selector: 'a.ark-block-icon__frame',
				attribute: 'href',
			},
			rel: {
				type: 'string',
				source: 'attribute',
				selector: 'a.ark-block-icon__frame',
				attribute: 'rel',
			},
			isNewTab: {
				type: 'boolean',
				default: false,
			},
			// 本来ないが、2.20 以前のアイコンデータを引き継ぐために追加
			iconV1: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-icon__frame',
				default: '',
			},
		},
		migrate(attributes) {
			const newAttrs = {};
			if (attributes.icon) {
				newAttrs.icon = migrateIconName(attributes.icon);
				console.log('Icon icon migrated:', newAttrs.icon);
			}

			return {
				...attributes,
				...newAttrs,
			};
		},
		save: ({ attributes }) => {
			const {
				icon,
				label,
				style,
				padding,
				radius,
				iconHtml,
				size,
				iconColor,
				position,
				href,
				rel,
				isNewTab,
				iconV1,
			} = attributes;

			const oldSvg = iconV1 || '';
			// oldSvg = oldSvg.replace(/<span.*?<\/span>/g, ''); // arkb の span にはクラスがあることに注意
			// if (oldSvg) {
			// 	console.log('old icon:', oldSvg);
			// }

			const blockProps = useBlockProps.save({
				className: classnames(blockName, {
					[`is-style-${style}`]: style || false,
				}),
				style: {
					'--arkb-icon-size': size || null,
					'--arkb-icon-color': iconColor || null,
				},
			});

			const innerStyles = {
				marginLeft: position === 'left' ? '0' : null,
				marginRight: position === 'right' ? '0' : null,
			};

			const figureStyles = {
				borderRadius: radius || null,
				padding: padding ? getCusomPadding(padding, '0') : null,
				// boxSizing: isContentBox ? 'content-box' : null,
			};

			const linkData = !href
				? null
				: {
						href,
						rel: rel || null,
						target: isNewTab ? '_blank' : null,
				  };

			return (
				<div {...blockProps}>
					<div className={`${blockName}__inner`} style={innerStyles || null}>
						<IconContentV1
							icon={icon}
							label={label}
							iconHtml={iconHtml}
							oldIcon={oldSvg}
							className={`${blockName}__frame`}
							style={figureStyles || null}
							link={linkData}
						/>
					</div>
				</div>
			);
		},
	},
];
