/**
 * @WordPress dependencies
 */
import { __, _x } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { useCallback } from '@wordpress/element';
import {
	BlockControls,
	InspectorControls,
	useBlockProps,
	PanelColorSettings,
	JustifyContentControl,
	// BlockAlignmentControl,
} from '@wordpress/block-editor';
import {
	PanelBody,
	BaseControl,
	// ToggleControl,
	// TextareaControl,
	TextControl,
} from '@wordpress/components';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import blockIcon from './icon';
import deprecated from './deprecated';
import { getCusomPadding } from '@helper';
import {
	ArkbUnitNumber,
	ArkbMarginControl,
	LsSpaceControl,
	LsIcon,
	LsIconPicker,
	ArkbLinkControl,
	ArkbStyleControls,
} from '@components';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const PREVIEW_ICON = 'LsStarFull';
const SPECIFIED_PADDING = '16%';

const STYLES = [
	{
		name: '',
		value: '',
		label: _x('Icon only', 'style', 'arkhe-blocks'),
	},
	{
		name: 'outline',
		value: 'outline',
		label: _x('Outline', 'style', 'arkhe-blocks'),
	},
	{
		name: 'fill',
		value: 'fill',
		label: _x('Fill', 'style', 'arkhe-blocks'),
	},
];

/**
 * svgタグにクラスを付与
 */
function addClassToSvg(html, addClass) {
	// svgタグにclassを当てる
	const svgMatched = html.match(/<svg.*?>/);
	if (!svgMatched) return html;

	const svgTag = svgMatched[0];

	let newSvgTag = svgTag;

	// すでに追加したいクラス名を持つ場合は何もしない
	const searchClassPattern = new RegExp(`${addClass}`);
	if (-1 !== svgTag.search(searchClassPattern)) {
		return html;
	}

	// クラスを持つかどうか。(クオートコメントはPoeditのエラー回避用)
	if (
		-1 !== svgTag.search(/class=["']/) // '"
	) {
		newSvgTag = svgTag.replace(/class=(["'])/, `class=$1${addClass} `); //'"
	} else {
		newSvgTag = svgTag.replace('<svg', `<svg class="${addClass}"`);
	}

	return html.replace(svgTag, newSvgTag);
}

const Preview = ({ value = 'fill' }) => {
	return (
		<div className={`ark-block-icon is-style-${value}`}>
			<div className='ark-block-icon__inner'>
				<IconContent icon={PREVIEW_ICON} className={`${blockName}__frame`} />
			</div>
		</div>
	);
};

/* eslint react/jsx-no-target-blank: 0 */
const IconContent = ({ icon, label, iconHtml, link, ...props }) => {
	// SVG手動入力の場合
	if (iconHtml) {
		// <svg>にclassを当てる
		iconHtml = addClassToSvg(iconHtml, `${blockName}__svg`);

		// リンクがある場合
		if (null !== link) {
			return (
				<a
					{...link}
					{...props}
					data-html='1'
					dangerouslySetInnerHTML={{
						__html: iconHtml,
					}}
				></a>
			);
		}

		return (
			<span
				{...props}
				data-html='1'
				dangerouslySetInnerHTML={{
					__html: iconHtml,
				}}
			/>
		);
	}

	// リンクがある場合
	if (null !== link) {
		return (
			<a {...link} {...props}>
				<LsIcon icon={icon} label={label || null} className={`${blockName}__svg`} />
			</a>
		);
	}

	return (
		<span {...props}>
			<LsIcon icon={icon} label={label || null} className={`${blockName}__svg`} />
		</span>
	);
};

/**
 * アイコン
 */
const blockName = 'ark-block-icon';
registerBlockType(metadata.name, {
	title: __('Icon', 'arkhe-blocks'),
	icon: blockIcon,
	// variations,
	edit: ({ attributes, setAttributes }) => {
		const {
			icon,
			label,
			style,
			padding,
			radius,
			iconHtml,
			size,
			iconColor,
			position,
			href,
			rel,
			isNewTab,
		} = attributes;

		const blockProps = useBlockProps({
			className: classnames(blockName, 'ark-has-guide', {
				[`is-style-${style}`]: style || false,
			}),
			style: {
				'--arkb-icon-size': size || null,
				'--arkb-icon-color': iconColor || null,
			},
		});

		const innerStyles = {
			marginLeft: position === 'left' ? '0' : null,
			marginRight: position === 'right' ? '0' : null,
		};

		const figureStyles = {
			borderRadius: radius || null,
			padding: padding ? getCusomPadding(padding, '0') : null,
			// boxSizing: isContentBox ? 'content-box' : null,
		};

		const setLink = ({ url: newUrl, rel: newRel, opensInNewTab }) => {
			if (newUrl) {
				setAttributes({ href: newUrl || undefined });
			}

			// undefinedチェックはコアのバグに対応するため
			if (typeof opensInNewTab !== 'undefined') {
				setAttributes({
					isNewTab: opensInNewTab,
					rel: newRel,
				});
			}
		};

		const updateAlignment = (val) => {
			if ('center' === val) val = undefined;
			setAttributes({ position: val });
		};

		const onChangeStyle = useCallback(
			(val) => {
				const newAttrs = { style: val };
				if (!val) {
					newAttrs.padding = undefined;
					newAttrs.radius = undefined;
				} else {
					if (!radius) {
						newAttrs.radius = '50%';
					}
					if (!padding) {
						newAttrs.padding = {
							top: SPECIFIED_PADDING,
							left: SPECIFIED_PADDING,
							right: SPECIFIED_PADDING,
							bottom: SPECIFIED_PADDING,
						};
					}
				}
				setAttributes(newAttrs);
			},
			[radius, padding, setAttributes]
		);
		// const dynamicStyles = getDynamicStyles({ clientId,});

		return (
			<>
				{/* {dynamicStyles && <DynamicStyles style={dynamicStyles} />} */}
				<BlockControls group='block'>
					{/* <BlockAlignmentControl ... /> */}
					<JustifyContentControl
						allowedControls={['left', 'center', 'right']}
						value={position || 'center'}
						onChange={updateAlignment}
					/>
					<ArkbLinkControl
						attrs={{ url: href, rel, isNewTab }}
						setLink={setLink}
						removeLink={() => {
							setAttributes({
								href: undefined,
								rel: undefined,
								isNewTab: false,
							});
						}}
						setRel={(val) => {
							setAttributes({ rel: val || undefined });
						}}
					/>
				</BlockControls>
				<BlockControls>
					<ArkbMarginControl {...{ className: attributes.className, setAttributes }} />
				</BlockControls>
				<InspectorControls>
					<PanelBody title={__('Style', 'arkhe-blocks')} initialOpen={true}>
						<ArkbStyleControls
							type='arkb-icon'
							styles={STYLES}
							nowValue={style || ''}
							setValue={onChangeStyle}
							defaultValue={STYLES[0].value}
							Preview={Preview}
							scale='0.8'
							height='50%'
						/>
					</PanelBody>
					<PanelBody title={__('Icon', 'arkhe-blocks')} initialOpen={true}>
						<BaseControl>
							<BaseControl.VisualLabel>
								{__('Select Icon', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<LsIconPicker
								value={icon}
								svg={iconHtml}
								onChange={(val) => {
									setAttributes({ icon: val, iconHtml: undefined });
								}}
								onSetSvg={(val) => {
									setAttributes({ iconHtml: val, icon: '' });
								}}
								clearable={false}
							/>
						</BaseControl>
						<TextControl
							className='arkb-iconNameInput'
							label={__('aria-label for Icon', 'arkhe-blocks')}
							value={label || ''}
							onChange={(val) => {
								setAttributes({ label: val });
							}}
						/>
					</PanelBody>
					<PanelColorSettings
						title={__('Settings', 'arkhe-blocks')}
						initialOpen={true}
						colorSettings={[
							{
								value: iconColor,
								label: __('Icon color', 'arkhe-blocks'),
								onChange: (val) => {
									setAttributes({ iconColor: val });
								},
							},
						]}
					>
						<BaseControl className='u-arkb--mt--15'>
							<BaseControl.VisualLabel>
								{__('Icon size', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbUnitNumber
								value={size}
								onChange={(val) => {
									setAttributes({ size: val });
								}}
								onClear={() => setAttributes({ size: '5em' })}
							/>
						</BaseControl>
						<BaseControl className='u-arkb--mt--15'>
							<BaseControl.VisualLabel>
								{__('Rounded corners', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<ArkbUnitNumber
								value={radius || '0px'}
								units={['px', 'em', '%']}
								widthRange={true}
								onChange={(val) => {
									setAttributes({ radius: val });
								}}
								onClear={() => setAttributes({ radius: undefined })}
								// default={}
							/>
						</BaseControl>
						<BaseControl className='u-arkb--mt--15'>
							<BaseControl.VisualLabel>
								{__('Padding', 'arkhe-blocks')}
							</BaseControl.VisualLabel>
							<LsSpaceControl
								name='padding'
								value={padding}
								setAttributes={setAttributes}
								defaultUnit='%'
								clearable={true}
							/>
						</BaseControl>
					</PanelColorSettings>
				</InspectorControls>
				<div {...blockProps}>
					<div className={`${blockName}__inner`} style={innerStyles || null}>
						<IconContent
							icon={icon}
							label={label}
							iconHtml={iconHtml}
							link={
								href
									? {
											href,
											rel: rel || null,
											target: isNewTab ? '_blank' : null,
									  }
									: null
							}
							className={`${blockName}__frame`}
							style={figureStyles || null}
						/>
					</div>
				</div>
			</>
		);
	},

	save: ({ attributes }) => {
		const {
			icon,
			label,
			style,
			padding,
			radius,
			iconHtml,
			size,
			iconColor,
			position,
			href,
			rel,
			isNewTab,
		} = attributes;

		const blockProps = useBlockProps.save({
			className: classnames(blockName, {
				[`is-style-${style}`]: style || false,
			}),
			style: {
				'--arkb-icon-size': size || null,
				'--arkb-icon-color': iconColor || null,
			},
		});

		const innerStyles = {
			marginLeft: position === 'left' ? '0' : null,
			marginRight: position === 'right' ? '0' : null,
		};

		const figureStyles = {
			borderRadius: radius || null,
			padding: padding ? getCusomPadding(padding, '0') : null,
			// boxSizing: isContentBox ? 'content-box' : null,
		};

		const linkData = !href
			? null
			: {
					href,
					rel: rel || null,
					target: isNewTab ? '_blank' : null,
			  };

		return (
			<div {...blockProps}>
				<div className={`${blockName}__inner`} style={innerStyles || null}>
					<IconContent
						icon={icon}
						label={label}
						iconHtml={iconHtml}
						className={`${blockName}__frame`}
						style={figureStyles || null}
						link={linkData}
					/>
				</div>
			</div>
		);
	},
	deprecated,
});
